.navBack {
  height: 70px;
  margin-top: -70px;
}

.tuition_herosection {
  background:
    url("../../assets/images/tuitionClassesHeroImg.png"), white;
  min-height: 600px;
  // background-attachment: fixed;
  background-size: cover;
  background-position-y: -120px;
  clip-path: ellipse(160% 75% at 50% 25%);
  padding: 2rem 4rem;
  z-index: -1;

  h2{
    font-size: 4rem;
    color: transparent;
    font-family: var(--text-font);
    font-weight: 900;
    background: linear-gradient(to bottom right, var(--primary-color), var(--purple));
    background-clip: text;
    max-width: 15ch;
    line-height: 4.5rem;
    margin-bottom: 1rem;
    margin-top: 2rem;
  }

  h3{
    font-size: 3rem;
    color: var(--light-gray);
    font-family: var(--primary-font);
    font-weight: 500;
    max-width: 25ch;
  }

  button{
    font-family: var(--text-font);
    font-weight: 500;
    border: 1.6px solid #fff;
    padding: 0.5rem 1.4rem;
    border-radius: 40px;
    color: var(--purple);
    cursor: pointer;
    font-size: 1.2rem;
    box-shadow: 0 2px 2px 0 rgb(0 0 0 / 35%);
    margin-top: 2rem;
    transition: .44s ease-in-out;

    &:hover{
        border-color: var(--purple);
        color: white;
        background-color: var(--purple);
    }
  }
}


.small-box{
    max-width: 600px;
    padding: 20px;
    border-radius: 20px;
    margin: 1rem auto;
    box-shadow: 0 2px 10px rgb(0 0 0 / .3);
    


    h3{
        font-family: var(--primary-font);
        font-weight: 600;
        font-size: 1.2rem;
    }

    ol li {
        input{
            accent-color: var(--primary-color);
            pointer-events: none;
        }

        label{
            font-family: var(--text-font);
            font-weight: 1.1rem;
        }
    }

    h4{
        font-family: var(--text-font);
        font-size: 1.1rem;
        font-weight: 500;
    }
}


.categories{
    margin-top: 2rem;
    // transform: translateY(-2rem);
    background-color: #e5e7eb;
    padding: 6rem 10px 2rem 10px;
    clip-path: ellipse(120% 85% at 50% 86%);

    h2{
        font-family: var(--primary-font);
        font-size: 2.5rem;
        font-weight: 400;
    }

    .sub-heading{
        margin: auto;
        margin-bottom: 4rem;
        width: fit-content;
        position: relative;
        

        &::before, &::after{
            content: '';
            position: absolute;
            height: 2px;
            width: 3rem;
            border-radius: 5px;
            background-color: var(--purple);
        }

        &::before{
            left: -120%;
        }

        &::after{
            right: -120%;
        }
    }

    .cards-container{
        justify-content: center;


        .card{
            padding: 15px;
            max-width: 350px;
            box-shadow: 0 2px 10px rgb(0 0 0 / .3);
            border-radius: 25px;
            background-color: white;

            h3, h4{
                font-family: var(--primary-font);
            }

            h3{
                font-weight: 500;
                margin-top: 1rem;
            }

            h4{
                font-weight: 400;
                margin-top: 10px;
                color: gray;
                margin-bottom: 5px;
            }

            figure{
                img{
                    width: 100%;
                    border-top-left-radius: 25px;
                    border-top-right-radius: 25px;
                }
            }
        }
    }
}



@media screen and (max-width: 600px){
    .tuition_herosection{
        padding: 2rem;
        background-position-x: -130%;
    }

    .tuition_herosection h2 {
        font-size: 3.5rem;
    }

    .categories{
        clip-path: ellipse(180% 85% at 50% 86%);
    }
}