.why_sasa-container{gap:5rem;background-color:#f2f2f2}.why_sasa-container>*{flex:1 1 400px}.why_sasa-container .left-content h2.why-ques{font-size:3.5rem;font-weight:600;color:rgba(0,0,0,0);background:linear-gradient(to right, var(--purple), var(--primary-color));background-clip:text;margin-bottom:2rem;font-family:var(--primary-font)}.why_sasa-container .left-content .why-ans{gap:10px}.why_sasa-container .left-content .why-ans p{font-size:1rem;font-weight:400;line-height:1.7rem}.why_sasa-container .left-content .why-ans p span{font-weight:500;font-style:italic}.why_sasa-container .left-content ul{width:fit-content;align-items:flex-start;margin-top:2rem;margin-left:2rem}.why_sasa-container .left-content ul li{font-family:var(--primary-font);font-size:1rem;color:var(--light-gray);list-style:circle;font-weight:500}.why_sasa-container .left-content ul li::marker{content:"✔";color:var(--primary-color);border:1px solid var(--primary-color)}.why_sasa-container .right-content .title{font-family:var(--primary-font);color:var(--light-gray);text-transform:uppercase;position:relative;margin-left:1rem;margin-bottom:1rem}.why_sasa-container .right-content .title::before{content:"";position:absolute;height:10px;width:10px;background-color:var(--primary-color);left:-2%;top:50%;transform:translate(-50%, -50%)}.why_sasa-container .right-content ol{list-style:none;padding:0;margin:2rem 0;position:relative;counter-reset:ol-counter}.why_sasa-container .right-content ol::before{content:"";position:absolute;width:.5px;height:90%;left:1.5rem;top:50%;transform:translate(-50%, -50%);background-color:#ccc}.why_sasa-container .right-content li{position:relative;font-family:var(--text-font);font-size:.9rem;display:flex;align-items:center;gap:.5rem;counter-increment:ol-counter}.why_sasa-container .right-content li::before{content:counter(ol-counter);background:#222;font-size:1rem;color:var(--primary-color);line-height:1;font-weight:900;display:grid;place-items:center;width:1.5rem;aspect-ratio:1;border:3px solid var(--primary-color);border-radius:50%;z-index:1}